import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HelloComponent} from "./pages/hello/hello.component";
import {WebDesignComponent} from "./pages/web-design/web-design.component";
import {WebAppsComponent} from "./pages/web-apps/web-apps.component";
import {VisualisationsComponent} from "./pages/visualisations/visualisations.component";
import {HelloSnippetComponent} from "./snippets/hello-snippet/hello-snippet.component";
import {WebDesignSnippetComponent} from "./snippets/web-design-snippet/web-design-snippet.component";
import {WebAppsSnippetComponent} from "./snippets/web-apps-snippet/web-apps-snippet.component";
import {VisualisationsSnippetComponent} from "./snippets/visualisations-snippet/visualisations-snippet.component";

const routes: Routes = [
  // <editor-fold desc="Pages">
  {
    path: '1',
    pathMatch: 'full',
    component: HelloComponent,
    // outlet: 'pages'
  },
  {
    path: '2',
    pathMatch: 'full',
    component: WebDesignComponent,
    // outlet: 'pages'
  },
  {
    path: '3',
    pathMatch: 'full',
    component: WebAppsComponent,
    // outlet: 'pages'
  },
  {
    path: '4',
    pathMatch: 'full',
    component: VisualisationsComponent,
    // outlet: 'pages'
  },
  // </editor-fold>
  // <editor-fold desc="Snippets">
  {
    path: '1',
    pathMatch: 'full',
    component: HelloSnippetComponent,
    outlet: 'snippets'
  },
  {
    path: '2',
    pathMatch: 'full',
    component: WebDesignSnippetComponent,
    outlet: 'snippets'
  },
  {
    path: '3',
    pathMatch: 'full',
    component: WebAppsSnippetComponent,
    outlet: 'snippets'
  },
  {
    path: '4',
    pathMatch: 'full',
    component: VisualisationsSnippetComponent,
    outlet: 'snippets'
  },
  // </editor-fold>
  {
    path: '',
    pathMatch: 'full',
    redirectTo: '/1(snippets:1)'
  },
  {
    path: '**',
    pathMatch: 'full',
    redirectTo: '/1(snippets:1)'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }



