import {AfterViewInit, Component, OnInit} from '@angular/core';
import {Observable} from "rxjs";
import {AppState} from "./shared/models/state.model";
import {StateService} from "./shared/services/state.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit {

  public state: AppState;

  constructor(private stateService: StateService, private router: Router) {}

  ngOnInit() {
    this.stateService.state$.subscribe((newState: AppState) => {
      this.state = newState;
    });

    this.router.navigate(['/']).then(() => {
      window.scrollTo(0, 0);

      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 500);
    });
  }

  ngAfterViewInit() {
    this.addRippleEffect();
    this.addShrinkEffect();
  }

  private addShrinkEffect() {
    Observable.merge(Observable.fromEvent(window, 'scroll'), Observable.fromEvent(window, 'touchmove')).subscribe((event: UIEvent | TouchEvent) => {
      if (window.scrollY > 0) {
        this.stateService.changeState({...this.state, landingSize: 'shrink', footer: 'hidden', navigationSnippet: 'hidden', gridShow: 'show'});
      } else if (window.scrollY === 0) {
        this.stateService.changeState({...this.state, landingSize: 'grow', footer: 'shown', navigationSnippet: 'shown', gridShow: 'hide'});
      }
    });
  }

  private addRippleEffect() {
    Observable.fromEvent(document.body, 'click').subscribe(function(e: any) {
      if (!(e.path.find((path: HTMLElement) => path && path.classList && path.classList.contains('clickable')))) {
        return;
      }

      const body = document.body;

      if (body.querySelectorAll('.ink').length === 0) {
        const span = document.createElement('span');
        span.classList.add('ink');
        body.insertBefore(span, body.firstChild);
      }

      const ink = <HTMLElement>body.querySelectorAll('.ink')[0];

      ink.classList.remove('animate');

      ink.style.height = '100vh';
      ink.style.width = '100vh';

      const rect = body.getBoundingClientRect();

      const offset = {
        top: rect.top + document.body.scrollTop,
        left: rect.left + document.body.scrollLeft
      };

      const x = e.pageX - offset.left - ink.offsetWidth / 2;
      const y = e.pageY - offset.top - ink.offsetHeight / 2;


      ink.style.top = `${y}px`;
      ink.style.left = `${x}px`;
      ink.classList.add('animate');

      body.classList.add('ripple');
    });
  }
}



