import {AfterViewInit, Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import Typed from 'typed.js/lib/typed.js';

@Component({
  selector: 'app-hello',
  templateUrl: './hello.component.html',
  styleUrls: ['./hello.component.scss']
})
export class HelloComponent implements OnInit, AfterViewInit {

  constructor(public route: ActivatedRoute) {}

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.initShadowTitle();
  }

  private initShadowTitle() {
    setTimeout(() => {
      for (let i = 0; i <= 3; i++) {
        document.getElementById('hello-shadow-title-' + i).innerText = '';
      }

      document.getElementById('hello-shadow-title-0').parentElement.classList.remove('col-content');
      document.getElementById('hello-shadow-title-0').parentElement.style.overflow = 'visible';

      const typed = new Typed('#hello-shadow-title-0', {
        strings: ['Web Enthusiast', 'Web Developer', 'Web Agnostic', 'Web Master'],
        typeSpeed: 100,
        loop: true
      });
    }, 1500);
  }

}



