import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {Observable} from 'rxjs';
import {StateService} from "../../services/state.service";
import {AppState} from "../../models/state.model";
import {animate, animateChild, group, query, state, style, transition, trigger} from "@angular/animations";
import * as d3 from 'd3';
import {ActivatedRoute, Router} from "@angular/router";
import * as Hammer from 'hammerjs';

@Component({
  selector: 'app-content-navigation',
  templateUrl: './content-navigation.component.html',
  styleUrls: ['./content-navigation.component.scss'],
  animations: [
    trigger('navigationSideExtras', [
      state('hidden', style({
        opacity: 0
      })),
      state('shown', style({
        opacity: 1
      })),
      transition('shown => hidden', animate('200ms')),
      transition('hidden => shown', animate('200ms 200ms')),
    ]),

    trigger('navigationHeaderIndexes', [
      state('hidden', style({
        opacity: 0
      })),
      state('shown', style({
        opacity: 1
      })),
      transition('shown => hidden', animate('200ms')),
      transition('hidden => shown', animate('200ms 200ms')),
    ]),

    trigger('navigationHeaders', [
      state('hidden', style({
        transform: '*'
      })),
      state('shown', style({
        transform: 'translateX(0)'
      })),
      transition('hidden => shown', [
        group([
          animate('300ms', style({
            transform: 'translateX(0)'
          })),
          query('@navigationHeaderIndexes', animateChild(), {optional: true})
        ])
      ]),
      transition('shown => hidden', [
        group([
          animate('300ms', style({
            transform: '*'
          })),
          query('@navigationHeaderIndexes', animateChild(), {optional: true})
        ])
      ]),
    ]),

    trigger('navigationSnippet', [
      state('hidden', style({
        opacity: 0
      })),
      state('shown', style({
        opacity: 1
      })),
      transition('shown <=> hidden', animate('200ms')),
    ]),
  ]
})
export class ContentNavigationComponent implements OnInit, AfterViewInit {

  public state: AppState;

  @ViewChild('contentNavigation')
  private contentNavigation: ElementRef;

  private headingsContainer: HTMLElement;
  private headings: HTMLElement;
  private activeHeadingCol: Element;

  constructor(private stateService: StateService, private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.stateService.state$.subscribe((newState: AppState) => {
      this.state = newState;
    });
  }

  ngAfterViewInit() {
    this.headingsContainer = this.contentNavigation.nativeElement.querySelector('.navigation-headings-container');
    this.headings = this.contentNavigation.nativeElement.querySelector('.navigation-headings-container .row');
    this.activeHeadingCol = this.contentNavigation.nativeElement.querySelector('.navigation-headings-container .navigation-heading-col.active');

    this.waitForDrag();
  }

  private waitForDrag() {
    const hm = new Hammer.Manager(this.contentNavigation.nativeElement);
    const headingsTranslateOrigin = this.headingsContainer.getBoundingClientRect().left - this.getOffset(this.activeHeadingCol).left;
    const headingsTranslateExtent = this.headingsContainer.getBoundingClientRect().left - this.getOffset(this.headings.children[this.headings.children.length - 1]).left;

    hm.add( new Hammer.Pan({ direction: Hammer.DIRECTION_HORIZONTAL, threshold: 0 }) );
    hm.add( new Hammer.Press());

    const translateObservable = Observable.combineLatest(
      Observable.merge(
        Observable.fromEvent(this.contentNavigation.nativeElement, 'mousedown'),
        Observable.fromEvent(this.contentNavigation.nativeElement, 'touchstart')
          .map((touchStartEvent: TouchEvent) =>
            new MouseEvent('mousedown', {clientX: touchStartEvent.touches[0].clientX, clientY: touchStartEvent.touches[0].clientY})),
      ),
      Observable.merge(
        Observable.fromEvent(this.contentNavigation.nativeElement, 'mousemove').throttle(() => Observable.timer(100)),
        Observable.fromEvent(hm, 'panleft panright')
          .map((pressEvent: any) => new MouseEvent('mousemove', {clientX: pressEvent.center.x, clientY: pressEvent.center.y}))
      )
    ).takeUntil(
      Observable.merge(
        Observable.fromEvent(this.contentNavigation.nativeElement, 'touchend'),
        Observable.fromEvent(this.contentNavigation.nativeElement, 'mouseup'),
        Observable.fromEvent(this.contentNavigation.nativeElement, 'mouseleave')
      )
    ).do((mouseEvents) => {
      (<MouseEvent>mouseEvents[0]).preventDefault();
      (<MouseEvent>mouseEvents[1]).preventDefault();
    });

    translateObservable.subscribe((mouseEvents) => {
      this.stateService.changeState({
        ...this.state,
        navigationSideExtras: 'hidden',
        navigationHeaders: 'shown',
        navigationHeaderIndexes: 'shown',
        landingTheme: 'dark'
      });
      const mouseDownEvent: MouseEvent = <MouseEvent>mouseEvents[0];
      const mouseMoveEvent: MouseEvent = <MouseEvent>mouseEvents[1];
      // <editor-fold desc="tilt the tiltbox"
      {
        const tiltBox: Element = document.getElementsByClassName('tilt-box')[0];
        const tiltScaleX = d3.scaleLinear().domain([0, document.body.getBoundingClientRect().width]).range([0, tiltBox.getBoundingClientRect().width]);
        tiltBox.dispatchEvent(new MouseEvent('mousemove', {
          clientX: tiltScaleX(mouseMoveEvent.clientX) + tiltBox.getBoundingClientRect().left
        }));
      }
      // </editor-fold"
      // <editor-fold desc="Translate headings"
      {
        let headingsTranslateLeft = headingsTranslateOrigin + (mouseMoveEvent.clientX - mouseDownEvent.clientX) * 1.2;
        // <editor-fold desc="RubberBand left">
        {
          headingsTranslateLeft = headingsTranslateLeft > 0 ?
            headingsTranslateLeft * 0.2 :
            headingsTranslateLeft;
        }
        // </editor-fold>
        // <editor-fold desc="RubberBand right">
        {
          headingsTranslateLeft = headingsTranslateLeft < headingsTranslateExtent ?
            headingsTranslateLeft + ((headingsTranslateExtent - headingsTranslateLeft) * (1 - 0.2)) :
            headingsTranslateLeft;
          this.headings.style.transform = `translateX(${headingsTranslateLeft}px) translateZ(0px)`;
        }
        // </editor-fold>
      }
      // </editor-fold"
      // <editor-fold desc="Get active heading"
      {
        if (this.activeHeadingCol !==
          this.getActiveHeading(this.headingsContainer.getBoundingClientRect().left, this.headings.querySelectorAll('.col.navigation-heading-col'))) {
          this.activeHeadingCol =
            this.getActiveHeading(this.headingsContainer.getBoundingClientRect().left, this.headings.querySelectorAll('.col.navigation-heading-col'));
          this.contentNavigation.nativeElement.querySelector('.navigation-headings-container .navigation-heading-col.active').classList.remove('active');
          this.activeHeadingCol.classList.add('active');
        }
      }
      // </editor-fold"
    });

    translateObservable.last(null, null, null).subscribe((mouseEvents) => {
      if (!mouseEvents) { return; }
      const mouseDownEvent: MouseEvent = <MouseEvent>mouseEvents[0];
      const mouseMoveEvent: MouseEvent = <MouseEvent>mouseEvents[1];
      // <editor-fold desc="if quick dragged"
      {
        if (this.headings.children[0].classList.contains('ng-animating') || this.headings.children[1].classList.contains('ng-animating')) {
          if (mouseDownEvent.clientX - mouseMoveEvent.clientX > 100) {
            this.activeHeadingCol = this.activeHeadingCol.nextElementSibling ? this.activeHeadingCol.nextElementSibling.nextElementSibling : this.activeHeadingCol;
            this.contentNavigation.nativeElement.querySelector('.navigation-headings-container .navigation-heading-col.active').classList.remove('active');
            this.activeHeadingCol.classList.add('active');
          } else if (mouseDownEvent.clientX - mouseMoveEvent.clientX < -100) {
            this.activeHeadingCol = this.activeHeadingCol.previousElementSibling ? this.activeHeadingCol.previousElementSibling.previousElementSibling : this.activeHeadingCol;
            this.contentNavigation.nativeElement.querySelector('.navigation-headings-container .navigation-heading-col.active').classList.remove('active');
            this.activeHeadingCol.classList.add('active');
          }
        }
      }
      // </editor-fold"
    }, null, () => {
      this.stateService.changeState({
        ...this.state,
        navigationSideExtras: 'shown',
        navigationHeaders: 'hidden',
        navigationHeaderIndexes: 'hidden',
        landingTheme: 'light'
      });

      const fromPageNumber = +this.router.url.match(/\d+/)[0];
      const toPageNumber = (Array.from(this.activeHeadingCol.parentElement.children).indexOf(this.activeHeadingCol) / 2) + 1;
      this.router.navigate([toPageNumber], {
        fragment: fromPageNumber !== toPageNumber ?
          (fromPageNumber < toPageNumber ? 'right' : 'left') : this.route.snapshot.fragment}).then(() => {
        this.router.navigate([{outlets: {snippets: [toPageNumber]}}], {preserveFragment: true});
      });

      // <editor-fold desc="Snap active heading"
      {
        const headingsTranslateLeft = this.headingsContainer.getBoundingClientRect().left - this.getOffset(this.activeHeadingCol).left;
        this.headings.style.transform = `translateX(${headingsTranslateLeft}px) translateZ(0px)`;
      }
      // </editor-fold"
      // <editor-fold desc="Leave the tiltbox">
      const tiltBox: Element = document.getElementsByClassName('tilt-box')[0];
      tiltBox.dispatchEvent(new MouseEvent('mouseleave'));
      // </editor-fold>

      this.waitForDrag();
    });
  }

  private getOffset(el) {
    let _x = 0;
    let _y = 0;
    while ( el && !isNaN( el.offsetLeft ) && !isNaN( el.offsetTop ) ) {
      _x += el.offsetLeft - el.scrollLeft;
      _y += el.offsetTop - el.scrollTop;
      el = el.offsetParent;
    }
    return { top: _y, left: _x };
  }

  private getActiveHeading(headingsContainerOffsetLeft: number, headingColArray: NodeListOf<Element>): Element {
    const headingColOffsetLeftArray: number[] = Array.from(headingColArray).map((element: Element) => element.getBoundingClientRect().left);

    let index = 0;
    let diff = Math.abs(headingsContainerOffsetLeft - headingColOffsetLeftArray[index]);
    for (let val = 0; val < headingColOffsetLeftArray.length; val++) {
      const newdiffLeft = Math.abs(headingsContainerOffsetLeft - headingColOffsetLeftArray[val]);
      if (newdiffLeft < diff) {
        diff = newdiffLeft;
        index = val;
      }
    }

    return headingColArray[index];
  }

  get currentPage() {
    return this.router.url && this.router.url.match(/\d+/) && this.router.url.match(/\d+/)[0] || '';
  }
}



