import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import VanillaTilt from "vanilla-tilt";

@Component({
  selector: 'app-tilt-box',
  templateUrl: './tilt-box.component.html',
  styleUrls: ['./tilt-box.component.scss']
})
export class TiltBoxComponent implements OnInit, AfterViewInit {

  @ViewChild('toTilt')
  private toTilt: ElementRef;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    // <editor-fold desc="init tiltbox and its tilt position"
    {
      VanillaTilt.init(this.toTilt.nativeElement, {perspective: 1000, reset: true, axis: 'x', scale: 1.2});

      this.toTilt.nativeElement.dispatchEvent(new MouseEvent('mouseenter', {
        clientX: this.toTilt.nativeElement.getBoundingClientRect().left,
        clientY: 0
      }));
    }
    // </editor-fold"
  }

}



