import {Injectable} from '@angular/core';
import {AngularFireAction, AngularFireDatabase, DatabaseSnapshot} from "angularfire2/database";
import {ToDo} from "./to-do.models";
import {Observable} from "rxjs/Observable";

@Injectable()
export class ToDoService {

  private userKey: string;

  constructor(private db: AngularFireDatabase) {
    this.userKey = this.getUserKey();
  }

  public getUserKey(): string {
    let userKey = localStorage.getItem('userKey');
    if (userKey) {
      return userKey;
    } else {
      userKey = this.db.createPushId();
      localStorage.setItem('userKey', this.userKey);
      return userKey;
    }
  }

  public postTodo(toDo: ToDo) {
    this.db.list(this.userKey).push(toDo);
  }

  public getTodos(): Observable<ToDo[]> {
    return this.db.list<ToDo>(this.userKey).snapshotChanges()
      .map((changes: AngularFireAction<DatabaseSnapshot>[]) => {
      return changes.map(change => <ToDo>{key: change.key, ...change.payload.val()});
    });
  }

  public putTodo(toDo: ToDo) {
    this.db.list(this.userKey).set(toDo.key, toDo);
  }

}



