import {Injectable} from '@angular/core';
import {Observable} from "rxjs/Observable";
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import {AppState} from "../models/state.model";

@Injectable()
export class StateService {

  public state$: Observable<AppState>;
  private stateSource: BehaviorSubject<AppState>;

  constructor() {
    this.stateSource = new BehaviorSubject<AppState>({
      gridShow: 'hide',
      landingSize: 'grow',
      landingTheme: 'light',

      navigation: 'idle',
      navigationSideExtras: 'shown',
      navigationHeaders: 'hidden',
      navigationHeaderIndexes: 'hidden',
      navigationSnippet: 'shown',
      footer: 'shown',
    });

    this.state$ = this.stateSource.asObservable();
  }

  public changeState(newState: AppState) {
    this.stateSource.next(newState);
  }

}



