import {NgModule} from '@angular/core';

import {AppComponent} from './app.component';
import {GridsComponent} from './shared/components/grids/grids.component';
import {HeaderComponent} from './shared/components/header/header.component';
import {ContentNavigationComponent} from './shared/components/content-navigation/content-navigation.component';
import {FooterComponent} from './shared/components/footer/footer.component';
import {StateService} from './shared/services/state.service';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {TiltBoxComponent} from './shared/components/tilt-box/tilt-box.component';
import {AnimateOnScrollModule} from "ng2-animate-on-scroll";
import {HelloComponent} from './pages/hello/hello.component';
import {AppRoutingModule} from "./app--routing.module";
import {WebDesignComponent} from './pages/web-design/web-design.component';
import {WebAppsComponent} from './pages/web-apps/web-apps.component';
import {AngularFireDatabase} from "angularfire2/database";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {VisualisationsComponent} from './pages/visualisations/visualisations.component';
import {HelloSnippetComponent} from './snippets/hello-snippet/hello-snippet.component';
import {VisualisationsSnippetComponent} from './snippets/visualisations-snippet/visualisations-snippet.component';
import {WebAppsSnippetComponent} from './snippets/web-apps-snippet/web-apps-snippet.component';
import {WebDesignSnippetComponent} from './snippets/web-design-snippet/web-design-snippet.component';

@NgModule({
  declarations: [
    AppComponent,
    GridsComponent,
    HeaderComponent,
    ContentNavigationComponent,
    FooterComponent,
    TiltBoxComponent,
    HelloComponent,
    WebDesignComponent,
    WebAppsComponent,
    VisualisationsComponent,
    HelloSnippetComponent,
    VisualisationsSnippetComponent,
    WebAppsSnippetComponent,
    WebDesignSnippetComponent
  ],
  imports: [
    // BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    AnimateOnScrollModule.forRoot()
    // AngularFireModule.initializeApp(environment.firebase),
    // AngularFirestoreModule, // imports firebase/firestore, only needed for database features
  ],
  providers: [StateService, AngularFireDatabase],
  bootstrap: [AppComponent]
})
export class AppModule {
}



