import {AfterViewInit, Component, HostBinding, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {animate, style, transition, trigger} from "@angular/animations";
import * as d3 from 'd3';
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'app-visualisations',
  templateUrl: './visualisations.component.html',
  styleUrls: ['./visualisations.component.scss'],
  animations: [
    trigger('routerOutlet', [
      transition(':enter', [
        style({
          opacity: 0,
          position: 'absolute'
        }),
        animate('500ms ease-in-out', style({
          opacity: 1
        }))
      ]),
      transition(':leave', [
        style({
          opacity: 1,
          position: 'absolute'
        }),
        animate('500ms ease-in-out', style({
          opacity: 0
        }))
      ]),
    ])
  ]
})
export class VisualisationsComponent implements OnInit, AfterViewInit, OnDestroy {

  @HostBinding('@routerOutlet')
  routerOutletAnimation = true;

  @ViewChild('steamgraphContainer')
  private steamgraphContainer;

  private steamgraphIntervalRef: any;

  constructor(public route: ActivatedRoute) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.drawSteamgraph();
  }

  ngOnDestroy() {
    try {
      clearInterval(this.steamgraphIntervalRef);
    } finally {}
  }

  private drawSteamgraph() {
    const n = 20, // number of layers
      m = 200, // number of samples per layer
      k = 10; // number of bumps per layer

    let stack = d3.stack().keys(<any>d3.range(n)).offset(d3.stackOffsetWiggle),
      layers0 = stack(<any>d3.transpose(d3.range(n).map(function() { return bumps(m, k); }))),
      layers1 = stack(<any>d3.transpose(d3.range(n).map(function() { return bumps(m, k); }))),
      layers = layers0.concat(layers1);

    const svg = d3.select(this.steamgraphContainer.nativeElement),
      width = +svg.attr('width'),
      height = +svg.attr('height');

    const x = d3.scaleLinear()
      .domain([0, m - 1])
      .range([0, width]);

    const y = d3.scaleLinear()
      .domain([<any>d3.min(layers, stackMin), d3.max(layers, stackMax)])
      .range([height, 0]);

    const z = d3.interpolateCool;

    const area = d3.area()
      .x(function(d, i) { return x(i); })
      .y0(function(d) { return y(d[0]); })
      .y1(function(d) { return y(d[1]); });

    svg.selectAll('path')
      .data(layers0)
      .enter().append('path')
      .attr('d', area)
      .attr('fill', () => {const color = Math.random() * 200 + 20; return <any>d3.rgb(color, color, color); });

    function stackMax(layer) {
      return d3.max(layer, function(d) { return d[1]; });
    }

    function stackMin(layer) {
      return d3.min(layer, function(d) { return d[0]; });
    }

    function transitionValues() {
      stack = d3.stack().keys(<any>d3.range(n)).offset(d3.stackOffsetWiggle),
        layers0 = stack(<any>d3.transpose(d3.range(n).map(function() { return bumps(m, k); }))),
        layers1 = stack(<any>d3.transpose(d3.range(n).map(function() { return bumps(m, k); }))),
        layers = layers0.concat(layers1);

      let t;
      d3.selectAll('path')
        .data((t = layers1, layers1 = layers0, layers0 = t))
        .transition()
        .duration(4000)
        .ease(d3.easeSin)
        .attr('d', area);
    }

// Inspired by Lee Byrons test data generator.
    function bumps(n, m) {
      var a = [], i;
      for (i = 0; i < n; ++i) a[i] = 0;
      for (i = 0; i < m; ++i) bump(a, n);
      return a;
    }

    function bump(a, n) {
      var x = 1 / (0.1 + Math.random()),
        y = 2 * Math.random() - 0.5,
        z = 10 / (0.1 + Math.random());
      for (var i = 0; i < n; i++) {
        var w = (i / n - y) * z;
        a[i] += x * Math.exp(-w * w);
      }
    }

    this.steamgraphIntervalRef = setInterval(function(){
      transitionValues();
    }, 4000);
  }

}



