import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import Typed from 'typed.js/lib/typed.js';
import VanillaTilt from "vanilla-tilt";
import {animate, style, transition, trigger} from "@angular/animations";

@Component({
  selector: 'app-web-design',
  templateUrl: './web-design.component.html',
  styleUrls: ['./web-design.component.scss'],
  animations: [
    trigger('showCode', [
      transition(':enter', [
        style({
          position: 'absolute',
          opacity: 0
        }),
        animate('500ms 500ms ease-in-out', style({
          position: 'static',
          opacity: 1
        }))
      ]),
    ])
  ]
})
export class WebDesignComponent implements OnInit, AfterViewInit {

  public holygrailCode: string;

  @ViewChild('toTilt')
  private toTilt: ElementRef;

  @ViewChild('toResize')
  private toResize: ElementRef;

  private browserSizes: string[];

  constructor(public route: ActivatedRoute) {
    this.browserSizes = ['col-6', 'col-4', 'col-8'];
    this.holygrailCode = `
<div class="HolyGrail container-fluid h-100">
  <div class="row h-25">
    <header class="HolyGrail-header col">
      <span class="d-block w-100 h-100 p-2">1</span>
    </header>
  </div>
  <div class="HolyGrail-body row h-50">
    <main class="HolyGrail-content order-sm-0 order-lg-0 col-sm-12 col-lg-2">
      <span class="d-block w-100 h-100 p-2">2</span>
    </main>
    <nav class="HolyGrail-nav order-sm-2 order-lg- col-sm col-lg">
      <span class="d-block w-100 h-100 p-2">3</span>
    </nav>
    <aside class="HolyGrail-ads order-sm-1 order-lg-2 col-sm-2 col-lg-2">
      <span class="d-block w-100 h-100 p-2">4</span>
    </aside>
  </div>
  <div class="row h-25">
    <footer class="HolyGrail-footer col">
      <span class="d-block w-100 h-100 p-2">5</span>
    </footer>
  </div>
</div>`;
  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.toResize.nativeElement.style.transition = '1000ms ease-in-out';
    this.initShadowTitle();
    this.initToTilt();
    this.initTypingAnimations()
  }

  public resizeBrowser() {
    this.toResize.nativeElement.className = this.browserSizes[0];
    this.browserSizes.push(this.browserSizes.shift());
  }

  private initShadowTitle() {
    setTimeout(() => {
      for (let i = 0; i <= 3; i++) {
        document.getElementById('web-design-shadow-title-' + i).innerText = '';
      }

      document.getElementById('web-design-shadow-title-0').parentElement.classList.remove('col-content');
      document.getElementById('web-design-shadow-title-0').parentElement.style.overflow = 'visible';

      const typed = new Typed('#web-design-shadow-title-0', {
        strings: ['Responsive Design', 'Adaptive Design', 'Scalable Design'],
        typeSpeed: 100,
        loop: true
      });
    }, 1500);
  }

  private initToTilt() {
    VanillaTilt.init(this.toTilt.nativeElement, {perspective: 5000, reset: false, axis: 'x'});
  }

  private initTypingAnimations() {
    new Typed('#code-typing-sass', {
      strings: [
        `.browser-illustration .browser-body {
\t\tdisplay: block;
\t\tflex: 1;

\t\tpadding: 5px;

\t\t.HolyGrail-header, .HolyGrail-footer, .HolyGrail-content, .HolyGrail-nav, .HolyGrail-ads {
\t\t\tpadding: 10px;

\t\t\tspan {
\t\t\t\tborder: 1px solid $browser-color;
\t\t\t}
\t\t}

\t\t@include media-breakpoint-between(sm, lg) {
\t\t\tpadding: 15px;
\t\t}
\t}`
      ],
      typeSpeed: 50,
      loop: true
    });

    new Typed('#code-typing-svg', {
      strings: [
        ``
      ],
      typeSpeed: 50,
      loop: true
    });
  }

}



